import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {map, switchMap} from 'rxjs/operators';
import {HttpClient, HttpResponse} from '@angular/common/http';
import * as endPoints from '../../../../shared/serverEndpoints';
import * as PropertiesAction from './properties.actions';


@Injectable()
export class PropertiesEffects {

  constructor(public action: Actions, public http: HttpClient) {
  }

  // effects for properties
  @Effect()
  uploadPropertiesImages = this.action.pipe(
    ofType(PropertiesAction.UPLOAD_PROPERTIES_IMAGES),
    map((data: PropertiesAction.UploadPropertiesImages) => {
      return data.payload;
    }), switchMap((data: { registrationCinImage: File, registrationGstImage: File, registrationMsmeImage: File, registrationRcImage: File }) => {
      return this.http.post(endPoints.uploadPropertiesImages, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: PropertiesAction.SET_PROPERTIES,
            payload: response.body
          }
        ];
      }
    })
  );

  @Effect()
  editProperties = this.action.pipe(
    ofType(PropertiesAction.PROPERTIES_EDIT),
    map((payload: PropertiesAction.PropertiesEdit) => {
      return payload.payload;
    }),
    switchMap((data) => {
      return this.http.post(endPoints.editProperties, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: PropertiesAction.SET_PROPERTIES,
            payload: response.body
          }
        ];
      }
    })
  );

  @Effect()
  fetchProperties = this.action.pipe(
    ofType(PropertiesAction.TRY_FETCH_PROPERTIES),
    switchMap(() => {
      return this.http.get(endPoints.fetchProperties, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: PropertiesAction.SET_PROPERTIES,
            payload: response.body
          }
        ];
      }
    })
  );

}
