import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {PropertiesRoutingModule} from './properties-routing.module';
import {PropertiesComponent} from './properties.component';
import {PropertieseditComponent} from './propertiesedit/propertiesedit.component';
import {ReactiveFormsModule} from '@angular/forms';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import {StoreModule} from '@ngrx/store';
import {EffectsModule} from '@ngrx/effects';
import {PropertiesReducer} from './store/properties.reducer';
import {PropertiesEffects} from './store/properties.effects';


@NgModule({
  declarations: [
    PropertiesComponent,
    PropertieseditComponent
  ],
  imports: [
    ReactiveFormsModule,
    NgbModule,
    CommonModule,
    PropertiesRoutingModule,
    StoreModule.forFeature('properties', PropertiesReducer),
    EffectsModule.forFeature([PropertiesEffects])
  ]
})
export class PropertiesModule {
}
