import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {PropertiesComponent} from './properties.component';
import {PropertieseditComponent} from './propertiesedit/propertiesedit.component';


const routes: Routes = [
  {
    path: '', component: PropertiesComponent, children: [
      {path: 'dashboard', component: PropertiesComponent},
      {path: 'editProfile', component: PropertieseditComponent}
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PropertiesRoutingModule {
}
