// properties action
import {Action} from '@ngrx/store';

export const SET_PROPERTIES = 'SET_PROPERTIES';
export const TRY_FETCH_PROPERTIES = 'TRY_FETCH_PROPERTIES';
export const PROPERTIES_EDIT = 'PROPERTIES_EDIT';
export const UPLOAD_PROPERTIES_IMAGES = 'UPLOAD_PROPERTIES_IMAGES';


// classes for properties
export class UploadPropertiesImages {
  readonly type = UPLOAD_PROPERTIES_IMAGES;

  constructor(public payload: any) {
  }
}

export class PropertiesEdit implements Action {
  readonly type = PROPERTIES_EDIT;

  constructor(public payload: {}) {
  }
}

export class TryFetchProperties implements Action {
  readonly type = TRY_FETCH_PROPERTIES;
}

export class SetProperties implements Action {
  readonly type = SET_PROPERTIES;

  constructor(public payload: {}) {
  }
}

export type PropertiesActions =
  PropertiesEdit |
  TryFetchProperties |
  SetProperties;
