
import {PropertiesModel} from '../properties.model';
import {PropertiesActions} from './properties.actions';
import {AppState} from "../../../../store/app.reducer";

export interface PropertiesState extends AppState {
  properties: State;
}

export interface State {
  properties: PropertiesModel;
}

const initialState: State = {
  properties: new PropertiesModel('', '', '', '', '', '', '', '', '', '', {
    permanentAddress: {
      address: '',
      pincode: '',
      phoneNo: ''
    }
  }, {
    profileRegistrar: '', name: '', mobile: '', email: '', pan: '', address: {
      address: '',
      phoneNo: '',
      pincode: ''
    }, bankDetails: {
      bankAccountNo: '',
      bankAddress: '', bankIfsc: '', bankName: ''
    }
  }, '', '', '')
};

export function PropertiesReducer(state = initialState, action: PropertiesActions) {
  switch (action.type) {
    case 'SET_PROPERTIES': {
      return {
        ...state,
        properties: action.payload
      };
    }
    default: {
      return {
        ...state
      };
    }

  }
}
